@import url(https://fonts.googleapis.com/css?family=Merienda+One);
:root {
  --link-color: #FDA737;
}

html {
  height: 100%;
}

body {
  height: 100%;
  margin: 0;
  background-repeat: no-repeat;
  background-attachment: fixed;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to bottom, #0250c5 0%, #861da7 100%);
  background-repeat: no-repeat;
}

.custom-button {
  color: #f8f9fa;
  border-color: #f8f9fa;
}

.custom-button:hover {
  color: #FDA737;
  color: var(--link-color);
  background-color: #f8f9fa;
  border-color: #f8f9fa;
}
.nav-item:hover {
  cursor: pointer;
}

.Tilt {
  background-image: linear-gradient(10deg, #0250c5 0%, #861da7 80%);
}

.logo {
  width: 65%;
}
.image-input {
  padding: .30rem;
}

