.bounding-box {
  position: absolute;
  box-shadow: inset 0 0 0 3px #9f25e6;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  cursor: pointer;
}
.bg-transparent {
  background-color: transparent;
}

.orange {
  color: var(--link-color);
}

.App {
  text-align: center;
}

.title {
  font-family: 'Merienda One', cursive;
  color: #FDA737;
  font-size: 6rem;
  text-shadow: 2px 2px 2px #fac768;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
